.no-touch .process:hover .process__head:after {
  max-width: 100%;
}

.process__head {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 750ms;
  position: relative;
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 13px;
}

.process__head:after {
  transition: 300ms;
  content: '';
  position: absolute;
  width: 100%;
  max-width: 56px;
  height: 5px;
  background-color: #9013fe;
  left: 0;
  bottom: 0;
