}

.process__descr {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 750ms;
  position: relative;
  position: relative;
  color: #646464;
}

