.value:before {
  transition: opacity 300ms linear;
  content: '';
  position: absolute;
  width: calc((50% - 1242px/2) + 1242px * 0.4);
  background: linear-gradient(135deg, #5a96fa 0%, #59097c 100%);
  height: 100%;
  top: 0;
  right: 0;
  opacity: 0;
}

