.header__toggle-icon {
  transition: background-color 300ms;
  position: relative;
  width: 100%;
  height: 2px;
  background-color: #000;
}

.header__toggle-icon:before, .header__toggle-icon:after {
  transition-property: top, bottom, transform;
  transition-duration: 300ms;
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: #000;
}

.header__toggle-icon:before {
  top: -8px;
}

.header__toggle-icon:after {
  bottom: -8px;
}

.header_opened .header__menu {
  transition: transform 300ms linear;
  transform: translateY(100%) rotateX(0);
}

.header_opened .header__toggle-icon {
  background-color: transparent;
}

.header_opened .header__toggle-icon:before {
  top: 0;
  transform: rotate(45deg);
}

.header_opened .header__toggle-icon:after {
  bottom: 0;
  transform: rotate(-45deg);
