.cta__form {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 750ms;
  position: relative;
  transition-duration: 200ms;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.cta__input {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 750ms;
  position: relative;
  transition-duration: 200ms;
  position: relative;
  font-size: 18px;
  margin-top: 7px;
  min-height: 64px;
  max-width: 494px;
  width: 100%;
  border: 1px solid #d8d8d8;
  border-radius: 5px;
  padding: 24px;
}

.cta__input::placeholder {
  font-size: 14px;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  color: #6c6c6c;
}

.cta__input:focus {
  border-color: #9013fe;
}

.cta__input_invalid {
  border-color: red;
}

.cta__input_invalid + .cta__validation-message {
  opacity: 1;
  max-height: 20px;
}

.cta__validation-message {
  transition-property: opacity, max-height;
  transition-duration: 300ms;
  max-height: 0;
  opacity: 0;
  color: red;
  font-size: 12px;
  font-weight: 300;
}

.cta__textarea {
