  width: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.cta__file-label:before {
  flex-shrink: 0;
  content: '';
  display: block;
  width: 30px;
  height: 38px;
  background: url("../img/input-file.svg") no-repeat center;
