.estimation {
  color: #fff;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
}

.estimation__header {
  background-color: #000;
  padding: 70px 0;
}

.estimation__header_animate .estimation__title, .estimation__header_animate .estimation__descr {
  opacity: 0;
  transform: translateX(100px);
}

.estimation__title {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 750ms;
  position: relative;
}

.estimation__title:after {
  transition: background-color 1000ms;
}

.estimation__descr {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 750ms;
  position: relative;
  margin-top: 16px;
  max-width: 775px;
  font-size: 14px;
  line-height: 1.85;
}

.estimation__item {
  position: relative;
}

.estimation__item:not(:first-child):after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  top: 0;
  left: 0;
  background-color: #e6e6e6;
}
