.cta {
	@include helvetica;
	padding-top: 60px;
	padding-bottom: 80px;

	&__container {}

	&__title {
		@include animate;
		transition-duration: 200ms; }

	&__descr {
		@include animate;
		transition-duration: 200ms;
		margin-top: 20px;
		color: $text-dark-gray;
		font-weight: 700;
		line-height: 2.5;
		@include md {
			line-height: 1.4; } }

	&__subtitle {
		@include animate;
		transition-duration: 200ms;
		margin-top: 14px; }

	&__cta {
		@include animate;
		transition-duration: 200ms;
		margin-top: 24px;
		color: $purple;
		font-size: 32px;
		font-weight: 700; }

	&__form {
		@include animate;
		transition-duration: 200ms;
		display: flex;
		flex-direction: column;
		align-items: flex-start; }

	&__input {
		@include animate;
		transition-duration: 200ms;
		position: relative;
		font-size: 18px;
		margin-top: 7px;
		min-height: 64px;
		max-width: 494px;
		width: 100%;
		border: 1px solid #d8d8d8;
		border-radius: 5px;
		padding: 24px;
		&::placeholder {
			font-size: 14px;
			@include helvetica;
			color: #6c6c6c; }
		&:focus {
			border-color: $purple; } }

	&__input_invalid {
		border-color: red; }

	&__input_invalid + &__validation-message {
			opacity: 1;
			max-height: 20px; }

	&__validation-message {
		transition-property: opacity, max-height;
		transition-duration: 300ms;
		max-height: 0;
		opacity: 0;
		color: red;
		font-size: 12px;
		font-weight: 300; }


	&__textarea {
		resize: none; }

	&__file-label {
		@include animate;
		transition-duration: 200ms;
		margin-top: 30px;
		max-width: 494px;
		width: 100%;
		cursor: pointer;
		display: flex;
		align-items: center;
		&:before {
			flex-shrink: 0;
			content: '';
			display: block;
			width: 30px;
			height: 38px;
			background: url("../img/input-file.svg") no-repeat center;
			margin-right: 20px; }
		@include hover {
			text-decoration: none;
			color: rgba(#353535, .7); } }

	&__file-text {
		color: #353535; }

	&__input-file {
		display: none; }

	&__policy {
		@include animate;
		transition-duration: 200ms;
		margin-top: 20px;
		font-size: 14px;
		font-weight: 300;
		color: $text-dark-gray; }

	&__link {
		color: $text-dark-gray; }

	&__button {
		@include animate;
		transition-duration: 200ms;
		margin-top: 12px; }

	&__button-text {
		position: relative; }

	&__animate {
		opacity: 0;
		transform: translateY(-100px); } }
