.value {
	position: relative;
	transition: background 300ms;
	color: $black;
	background-color: #fafafa;
	&:before {
		transition: opacity 300ms linear;
		content: '';
		position: absolute;
		width: calc((50% - 1242px/2) + 1242px * 0.4);
		background: linear-gradient(135deg, #5a96fa 0%, #59097c 100%);
		height: 100%;
		top: 0;
		right: 0;
		opacity: 0; }
	@include r(1272) {
		&:before {
			width: 40%; } }
	@include md {
		&:before {
			width: 30%; } }
	@include sm {
		&:before {
			height: 77px;
			width: 100%;
			top: auto;
			bottom: 0; } }
	@include hover {
		&:before {
			opacity: 1; }
		.value__price {
			color: $white; } }

	&__container {
		opacity: 1;
		transition: opacity 2000ms;
		min-height: 250px;
		position: relative;
		display: flex;
		padding: 15px 15px;
		@include sm {
			flex-wrap: wrap; } }

	&__info {
		width: 60%;
		@include md {
			width: 70%; }
		@include sm {
			width: 100%; } }

	&__title {}

	&__descr {
		margin-top: 16px;
		font-size: 14px;
		line-height: 1.85;
		max-width: 605px; }

	&__price {
		transition: color 300ms;
		width: 40%;
		display: flex;
		justify-content: center;
		align-items: center;
		@include md {
			width: 30%;
			justify-content: flex-end;
			align-items: flex-end; }
		@include sm {
			width: 100%; } }

	&__price-text {
		text-transform: uppercase; }

	&__price-value {
		padding: 16px;
		font-size: 34px;
		font-weight: 700; } }

