.process {
	overflow: hidden;
	padding: 23px 40px;
	@include helvetica;
	width: 50%;
	max-width: calc(50% - 16px);
	margin-top: 30px;
	position: relative;
	@include sm {
		width: 100%;
		max-width: none;
		padding: 8px 12px; }
	//Hover
	@include hover {
		.process__head {
			&:after {
				max-width: 100%; } } }

	&__head {
		@include animate;
		position: relative;
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		padding-bottom: 13px;
		&:after {
			transition: 300ms;
			content: '';
			position: absolute;
			width: 100%;
			max-width: 56px;
			height: 5px;
			background-color: $purple;
			left: 0;
			bottom: 0; }
		@include sm {
			&:after {
				max-width: 36px; } } }


	&__count {
		transition: 300ms;
		position: relative;
		font-size: 50px;
		font-weight: 700;
		color: $purple;
		@include sm {
			font-size: 32px; } }

	&__title {
		transition: 300ms;
		margin-left: 18px; }

	&__descr {
		@include animate;
		position: relative;
		color: $text-gray; } }

