.nav {

	&__list {
		min-width: 410px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin-left: 0;
			display: flex;
			flex-direction: column; }
		@include sm {
			min-width: auto;
			width: 100%; } }

	&__item {
		@include animate;
		transition-duration: 350ms;
		@include md {
			position: relative;
			padding: 12px 0;
			text-align: center;
			&:before {
				content: '';
				position: absolute;
				height: 1px;
				top: 0;
				left: 15px;
				right: 15px;
				background-color: $gray; } } }

	&__link {
		color: black;
		@include hover;
		@include sm {
			letter-spacing: -0.4px; } } }
