.main-hero {
	margin-top: 65px;
	overflow: hidden;
	box-shadow: 0 6px 8px rgba(0, 0, 0, .5);
	@include helvetica;
	color: $white;
	position: relative;
	background: url("../img/hero-background.jpg") center no-repeat;
	background-size: cover;

	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba($darkgray, .8); }

	&__container {
		position: relative;
		min-height: 694px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center; }

	&__image {
		@include animate;
		transition-duration: 1500ms;
		content: '';
		pointer-events: none;
		position: absolute;
		background: url("../img/iphone-mac.png") no-repeat 815px;
		top: 0;
		left: 0;
		width: 200%;
		height: 100%; }

	&__image_animate {
		left: 200px;
		opacity: 0; }


	&__title {
		@include animate; }

	&__descr {
		@include animate;
		margin-top: 18px; }

	&__table {
		@include animate;
		max-width: 524px;
		margin-top: 24px; }

	&__table-row {}

	&__table-data {
		text-align: center;
		font-size: 32px;
		font-weight: 700; }


	&__table-head {
		text-align: center;
		font-weight: 300;
		font-size: 14px;
		text-transform: uppercase;
		padding: 0 8px; }

	&__button {
		@include animate;
		transition: transform 300ms, opacity 1000ms, background-color 300ms, border 300ms;
		margin-top: 54px; } }

.js-main-hero-anim {
	transition-duration: 300ms;
	transform: translateY(50px);
	opacity: 0; }
